import React, { useEffect, useState} from "react";
import axios from "axios";
import MatButton from "@material-ui/core/Button";
import {FormGroup, Label , CardBody, Spinner,Input,Form} from "reactstrap";
import * as moment from 'moment';
import {makeStyles} from "@material-ui/core/styles";
import {Card, CardContent} from "@material-ui/core";
import SaveIcon from "@material-ui/icons/Save";
// import AddIcon from "@material-ui/icons/Add";
// import CancelIcon from "@material-ui/icons/Cancel";
// import {ToastContainer, toast} from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "react-widgets/dist/css/react-widgets.css";
// import {Link, useHistory, useLocation} from "react-router-dom";
// import {TiArrowBack} from 'react-icons/ti'
import {token, url as baseUrl } from "../../../../api";
import 'react-phone-input-2/lib/style.css'
import { Button} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';
import "react-toastify/dist/ReactToastify.css";
import "react-widgets/dist/css/react-widgets.css";



const useStyles = makeStyles((theme) => ({
    card: {
        margin: theme.spacing(20),
        display: "flex",
        flexDirection: "column",
        alignItems: "center"
    },
    form: {
        width: "100%", // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    cardBottom: {
        marginBottom: 20,
    },
    Select: {
        height: 45,
        width: 300,
    },
    button: {
        margin: theme.spacing(1),
    },
    root: {
        flexGrow: 1,
        maxWidth: 752,
    },
    demo: {
        backgroundColor: theme.palette.background.default,
    },
    inline: {
        display: "inline",
    },
    error:{
        color: '#f85032',
        fontSize: '12.8px'
    }
}));


const BasicInfo = (props) => {
    const classes = useStyles();
    const [errors, setErrors] = useState({});
    const [kP, setKP] = useState([]);
    const [enrollSetting, setEnrollSetting] = useState([]);
    const [sourceReferral, setSourceReferral] = useState([]);
    const [gender, setGender] = useState([])
    const [states, setStates] = useState([]);
    const [provinces, setProvinces] = useState([]);
    const [maritalStatus, setMaritalStatus]= useState([]);
    const [ageDisabled, setAgeDisabled] = useState(true);

    useEffect(() => { 
        KP(); 
        EnrollmentSetting(); 
        SourceReferral();
        Genders();
        getStates();
        MaterialStatus();
    }, []);
    //Get list of KP
    const KP =()=>{
        axios
        .get(`${baseUrl}application-codesets/v2/KP_TYPE`,
            { headers: {"Authorization" : `Bearer ${token}`} }
        )
        .then((response) => {
            setKP(response.data);
        })
        .catch((error) => {
        //console.log(error);
        });    
    }
    //Get list of HIV STATUS ENROLLMENT
    const EnrollmentSetting =()=>{
        axios
        .get(`${baseUrl}application-codesets/v2/TEST_SETTING`,
            { headers: {"Authorization" : `Bearer ${token}`} }
        )
        .then((response) => {
            //console.log(response.data);
            setEnrollSetting(response.data);
        })
        .catch((error) => {
        //console.log(error);
        });    
    }
    //Get list of HIV STATUS ENROLLMENT
    const MaterialStatus =()=>{
        axios
        .get(`${baseUrl}application-codesets/v2/MARITAL_STATUS`,
            { headers: {"Authorization" : `Bearer ${token}`} }
        )
        .then((response) => {
            //console.log(response.data);
            setMaritalStatus(response.data);
        })
        .catch((error) => {
        //console.log(error);
        });    
    }
    //Get list of Source of Referral
    const SourceReferral =()=>{
            axios
            .get(`${baseUrl}application-codesets/v2/SOURCE_REFERRAL`,
                { headers: {"Authorization" : `Bearer ${token}`} }
            )
            .then((response) => {
                //console.log(response.data);
                setSourceReferral(response.data);
            })
            .catch((error) => {
            //console.log(error);
            });        
    }
    //Get list of Genders from 
    const Genders =()=>{
        axios
        .get(`${baseUrl}application-codesets/v2/SEX`,
            { headers: {"Authorization" : `Bearer ${token}`} }
        )
        .then((response) => {
            //console.log(response.data);
            setGender(response.data);
        })
        .catch((error) => {
        //console.log(error);
        });        
    }

    //Get States from selected country
    const getStates = () => {
        setStateByCountryId('1'); 
        setObjValues({ ...objValues, countryId: 1 });
    };
    //Get list of State
    function setStateByCountryId(getCountryId) {
        axios
        .get(`${baseUrl}organisation-units/parent-organisation-units/${getCountryId}`,
            { headers: {"Authorization" : `Bearer ${token}`} }
        )
        .then((response) => {
            setStates(response.data);
        })
        .catch((error) => {
        //console.log(error);
        });  
    }   
    //fetch province
    const getProvinces = e => {
            const stateId = e.target.value;
            setObjValues({ ...objValues, stateId: e.target.value });
            axios
            .get(`${baseUrl}organisation-units/parent-organisation-units/${stateId}`,
                { headers: {"Authorization" : `Bearer ${token}`} }
            )
            .then((response) => {
                setProvinces(response.data);
            })
            .catch((error) => {
            //console.log(error);
            });  
    };
    const [objValues, setObjValues]= useState(
        {
            active: true,
            clientCode: "",
            age:"",
            dob:"",
            dateVisit: "",
            firstTimeVisit: null,
            indexClient: null,
            numChildren: "",
            numWives: "",            
            dateOfBirth: null,
            dateOfRegistration:null,
            deceased: true,
            deceasedDateTime: null,
            educationId: "",
            employmentStatusId: "",
            facilityId: "",
            firstName: "",
            genderId: "",
            address: "",           
            isDateOfBirthEstimated: "",
            maritalStatusId: "",
            organizationId:"",
            otherName: "",
            sexId: "",
            surname: "",
            previouslyTested: "",
            referredFrom: "",
            targetGroup: "",
            testingSetting:"",
            typeCounseling: ""
        }
    )
    const handleInputChange = e => {        
        setObjValues ({...objValues,  [e.target.name]: e.target.value});               
    }
        //Date of Birth and Age handle 
        const handleDobChange = (e) => {
            if (e.target.value) {
                const today = new Date();
                const birthDate = new Date(e.target.value);
                let age_now = today.getFullYear() - birthDate.getFullYear();
                const m = today.getMonth() - birthDate.getMonth();
                if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
                    age_now--;
                }
                objValues.age=age_now
                //setBasicInfo({...basicInfo, age: age_now});        
            } else {
                setObjValues({...objValues, age:  ""});
            }
            setObjValues({...objValues, dob: e.target.value});
            
        }
        const handleDateOfBirthChange = (e) => {
            if (e.target.value == "Actual") {
                setAgeDisabled(true);
            } else if (e.target.value == "Estimated") {
                setAgeDisabled(false);
            }
        }
        const handleAgeChange = (e) => {
            if (!ageDisabled && e.target.value) {
                
                const currentDate = new Date();
                currentDate.setDate(15);
                currentDate.setMonth(5);
                const estDob = moment(currentDate.toISOString());
                const dobNew = estDob.add((e.target.value * -1), 'years');
                //setBasicInfo({...basicInfo, dob: moment(dobNew).format("YYYY-MM-DD")});
                objValues.dob =moment(dobNew).format("YYYY-MM-DD")
    
            }
            setObjValues({...objValues, age: e.target.value});
        }
        //End of Date of Birth and Age handling 
    const BasicDTO={
        "clientCode": "",
        "dateVisit": "",
        "extra": {},
        "firstTimeVisit": null,
        "indexClient": null,
        "numChildren": "",
        "numWives": "",
        "personDto": {
          "active": "",
          "address": [
            {
              "city": "",
              "countryId": "",
              "district": "",
              "line": [
                ""
              ],
              "organisationUnitId": "",
              "postalCode": "",
              "stateId": ""
            }
          ],
          "contact": [
            {
              "address": {
                "city": "",
                "countryId": "",
                "district": "",
                "line": [
                  ""
                ],
                "organisationUnitId": "",
                "postalCode": "",
                "stateId": ""
              },
              "contactPoint": {
                "type": "",
                "value": ""
              },
              "firstName": "",
              "genderId":"",
              "otherName": "",
              "relationshipId": "",
              "surname": ""
            }
          ],
          "contactPoint": [
            {
              "type": "",
              "value": ""
            }
          ],
          "dateOfBirth": null,
          "dateOfRegistration":null,
          "deceased": true,
          "deceasedDateTime": null,
          "educationId": "",
          "employmentStatusId": "",
          "facilityId": "",
          "firstName": "",
          "genderId": "",
          "id": "",
          "identifier": [
            {
              "assignerId":"",
              "type": "",
              "value": ""
            }
          ],
          "isDateOfBirthEstimated": "",
          "maritalStatusId": "",
          "organizationId":"",
          "otherName": "",
          "sexId": "",
          "surname": ""
        },
        "personId": "",
        "previouslyTested": "",
        "referredFrom": "",
        "targetGroup": "",
        "testingSetting":"",
        "typeCounseling": "",
        "breastFeeding":"",
        "pregnant":"",
        "clientIdentify":""
      }

      const handleItemClick =(e)=>{
        e.preventDefault();
        console.log(objValues)
        const page = 'pre-test-counsel'
        const completedMenu = 'basic'
        //props.handleItemClick(page)
        if(props.completed.includes(completedMenu)) {

        }else{
            props.setCompleted([...props.completed, completedMenu])
        }
    }

    return (
        <>
            
            <Card >
                <CardBody>
    
                <h2 style={{color:'#000'}}>BASIC INFORMATION</h2>
                <br/>
                    <form >
                        <div className="row">

                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Target Group *</Label>
                                    <select
                                        className="form-control"
                                        name="targetGroup"
                                        id="targetGroup"
                                        onChange={handleInputChange}
                                        value={objValues.targetGroup}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {kP.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.display}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">Client Code</Label>
                                <Input
                                    type="number"
                                    name="clientCode"
                                    id="clientCode"
                                    value={objValues.clientCode}
                                    onChange={handleInputChange}
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                />                                
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4"></div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Referred From *</Label>
                                    <select
                                        className="form-control"
                                        name="referredFrom"
                                        id="referredFrom"
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {sourceReferral.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.display}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Setting*</Label>
                                    <select
                                        className="form-control"
                                        name="testingSetting"
                                        id="testingSetting"
                                        value={objValues.testingSetting}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {enrollSetting.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.display}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">Visit Date  </Label>
                                <Input
                                    type="date"
                                    name="dateVisit"
                                    id="dateVisit"
                                    value={objValues.dateVisit}
                                    onChange={handleInputChange}
                                    max= {moment(new Date()).format("YYYY-MM-DD") }
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                    
                                />
                                {errors.dateOfEac1 !=="" ? (
                                    <span className={classes.error}>{errors.dateOfEac1}</span>
                                ) : "" }
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">First Name</Label>
                                <Input
                                    type="text"
                                    name="firstName"
                                    id="firstName"
                                    value={objValues.firstName}
                                    onChange={handleInputChange}
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                />
                                
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">Middle Name</Label>
                                <Input
                                    type="number"
                                    name="otherName"
                                    id="otherName"
                                    value={objValues.otherName}
                                    onChange={handleInputChange}
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                    
                                />
                                
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">Last Name</Label>
                                <Input
                                    type="number"
                                    name="surname"
                                    id="surname"
                                    value={objValues.surname}
                                    onChange={handleInputChange}
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                />
                                
                                </FormGroup>
                            </div>
                            <div className="form-group mb-2 col-md-2">
                                <FormGroup>
                                    <Label>Date Of Birth</Label>
                                    <div className="radio">
                                        <label>
                                            <input
                                                type="radio"
                                                value="Actual"
                                                name="dateOfBirth"
                                                defaultChecked
                                                
                                                onChange={(e) => handleDateOfBirthChange(e)}
                                                style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                            /> Actual
                                        </label>
                                    </div>
                                    <div className="radio">
                                        <label>
                                            <input
                                                type="radio"
                                                value="Estimated"
                                                name="dateOfBirth"
                                                
                                                onChange={(e) => handleDateOfBirthChange(e)}
                                                style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                            /> Estimated
                                        </label>
                                    </div>
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-3">
                                <FormGroup>
                                    <Label>Date</Label>
                                    <input
                                        className="form-control"
                                        type="date"
                                        name="dob"
                                        id="dob"
                                        max= {moment(new Date()).format("YYYY-MM-DD") }
                                        value={objValues.dob}
                                        onChange={handleDobChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    />
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-3">
                                <FormGroup>
                                    <Label>Age</Label>
                                    <input
                                        className="form-control"
                                        type="number"
                                        name="age"
                                        id="age"
                                        value={objValues.age}
                                        disabled={ageDisabled}
                                        onChange={handleAgeChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    />
                                </FormGroup>
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                <FormGroup>
                                <Label for="">Phone Number</Label>
                                <Input
                                    type="number"
                                    name="phoneNumber"
                                    id="phoneNumber"
                                    value={objValues.phoneNumber}
                                    onChange={handleInputChange}
                                    style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                />
                                
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>State *</Label>
                                    <select
                                        className="form-control"
                                        name="state"
                                        id="state"
                                        onChange={getProvinces}
                                        value={objValues.state}
                                    
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {states.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.name}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>LGA *</Label>
                                    <select
                                        className="form-control"
                                        name="lga"
                                        id="lga"
                                        value={objValues.lga}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {provinces.map((value, index) => (
                                            <option key={index} value={value.id}>
                                                {value.name}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Address *</Label>
                                    
                                    <Input
                                        type="textarea"
                                        name="address"
                                        id="address"
                                        value={objValues.address}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                    />
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Sex*</Label>
                                    <select
                                        className="form-control"
                                        name="sex"
                                        id="sex"
                                        value={objValues.sex}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        {gender.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.display}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Marital Status</Label>
                                    <select
                                        className="form-control"
                                        name="maritalStatusId"
                                        id="maritalStatusId"
                                        value={objValues.maritalStatusId}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                            {maritalStatus.map((value) => (
                                            <option key={value.id} value={value.id}>
                                                {value.display}
                                            </option>
                                        ))}
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Number of Children {'<5'} years</Label>
                                    <Input
                                        type="number"
                                        name="numChildren"
                                        id="numChildren"
                                        value={objValues.numChildren}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                    />   
                                </FormGroup>
                            </div>
                            {(objValues.maritalStatusId==='6' && objValues.age > 9) && (
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Number of wives/co-wives</Label>
                                    <Input
                                        type="number"
                                        name="numWives"
                                        id="numWives"
                                        value={objValues.numWives}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                    /> 
                                </FormGroup>
                            </div>
                            )}
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Index Testing</Label>
                                    <select
                                        className="form-control"
                                        name="indexClient"
                                        id="indexClient"
                                        value={objValues.indexClient}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="true">YES</option>
                                        <option value="false">NO</option>
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            {objValues.indexClient==='true' && (
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Client identified from an index client</Label>
                                    <select
                                        className="form-control"
                                        name="clientIdentify"
                                        id="clientIdentify"
                                        value={objValues.clientIdentify}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="Biological">Biological</option>
                                        <option value="Sexual">Sexual</option>
                                        <option value="Social">Social</option>
                                        
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            )}
                            {objValues.sex==='377' && (
                            <>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Pregnant</Label>
                                    <select
                                        className="form-control"
                                        name="pregnant"
                                        id="pregnant"
                                        value={objValues.pregnant}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="true">YES</option>
                                        <option value="false">NO</option>
                                    </select>
                                    
                                </FormGroup>
                            </div>

                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Breast Feeding</Label>
                                    <select
                                        className="form-control"
                                        name="breastFeeding"
                                        id="breastFeeding"
                                        value={objValues.breastFeeding}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="true">YES</option>
                                        <option value="false">NO</option>
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            </>
                            )}
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Index Client Code/ID</Label>
                                    <Input
                                        type="text"
                                        name="numWives"
                                        id="numWives"
                                        value={objValues.numWives}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.25rem"}}
                                   
                                    /> 
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>First time visit</Label>
                                    <select
                                        className="form-control"
                                        name="firstTimeVisit"
                                        id="firstTimeVisit"
                                        value={objValues.firstTimeVisit}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="true">YES</option>
                                        <option value="false">NO</option>
                                        
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Previously tested within the last 3 months</Label>
                                    <select
                                        className="form-control"
                                        name="previouslyTested"
                                        id="previouslyTested"
                                        value={objValues.previouslyTested}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value="true">YES</option>
                                        <option value="false">NO</option>
                                    </select>
                                    
                                </FormGroup>
                            </div>
                            <div className="form-group  col-md-4">
                                <FormGroup>
                                    <Label>Type of Counseling</Label>
                                    <select
                                        className="form-control"
                                        name="typeCounseling"
                                        id="typeCounseling"
                                        value={objValues.typeCounseling}
                                        onChange={handleInputChange}
                                        style={{border: "1px solid #014D88", borderRadius:"0.2rem"}}
                                    >
                                        <option value={""}></option>
                                        <option value={""}>Individual</option>
                                        <option value={""}>Couple</option>
                                        
                                    </select>
                                    
                                </FormGroup>
                            </div>

                            <br />
                            <div className="row">
                            <div className="form-group mb-3 col-md-6">
                            <Button content='Next' type="submit" icon='right arrow' labelPosition='right' style={{backgroundColor:"#014d88", color:'#fff'}} onClick={handleItemClick}/>
                            </div>
                            </div>
                        </div>
                    </form>
                </CardBody>
            </Card>                                 
        </>
    );
};

export default BasicInfo